.footerMain{
    background: black;
    min-height: 35px;
    display: flex;
}

.textForFooter{
    color: white;
}

.textBlock{
    display: flex;
    flex-direction: column;
}
.divHeaderImg{
    background: url(/vtuberhome/static/media/VtuberHomeLogo1.97864e81.png) no-repeat center;
    min-height: 350px;
    background-size: 100%;
}
nav{
    width: 100%;
    display: flex;
    flex-direction: row;
    
}

.btnWebsiteLinks{
    background: transparent;
    color: #fff;
    border: none;
    outline: none;
}

.btnWebsiteLinks:hover{
    background: white;
    color: black;
    border: none;
    outline: none;
}

.navMainNavBar .btnWebsiteLinks{
    font-size: 30px;
    padding: 6px;
}

.navMainNavBar div{
    font-size: 30px;
    
}

.formLanguageSelect{
    color: white;
    font-size: 30px;
    padding:6px;
}

.navMainNavBar a:hover{
    background-color: rgb(192, 192, 192);
    color: rgb(54, 0, 0);
    text-decoration: none;
}

.navMainNavBar{
    background: black;
    display: flex;
    flex-direction: row;
    min-height:40px;
}

.aWebsiteLinks{
    color: white;
    margin: 2px;
}
.divVtuberList{
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
    padding:4px;
    
    
}

.divPresentationRow{
    display:flex;
}

.divFilterBoxContainer{
    z-index: 2;
    max-width: 500px;
}
.divVtuberListItem{
    min-width: 340px;
    max-width:400px;
    min-height:400px;
    max-height:480px;
    margin: 2px;
    border-radius: 5%;
    align-content: center;
    justify-content: center;
    text-align: center;
    background:#f3f0ec;
    padding: 10px;
    color: #1E2749;
}

.divTitle{
    font-size: 20px;
}

.cardVtuberImage{
    max-width: 240px;
    max-height:240px;
}

.divVtuberCardBody{
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;

}

.spanFavoriteCheckBox{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-content: center;
}

.divChannelLink{
    max-width: 300px;
    text-overflow:wrap;
    word-wrap: none;
}


.divLiveStreamView{
    height: 100%;
    background:lightblue;
    overflow-y: scroll;
}
.divMainFavoriteView{
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
}
    /*
    TODO
    figure out how to set height to fill screen
        
    */

.divAboutMain{

    min-height: 100px;
    max-height: 100%;
    background: black;
    margin: 2px;
    border-radius: 25px;
    display: flex;
    flex-direction: column;
    overflow-y: scroll;

}

/*
TODO
add colors from the selected colorscheme*/
.pText{
    
}

.invisible{
    display: none;
}

.divMainFilter{
    background-color: transparent;
}

.btnFilterOpenClose{
    background: black;
    color: white;
    border: transparent;
    height: 50px;
    width: 100px;
    font-size: 30px;
    margin: 4px;
}

.divAddFilter{
    background: black;
    display: flex;
    flex-direction: column;
    position: fixed;
    left: 0;
    width: 0;
    z-index: 1;
    overflow-y:scroll;
}

.divFilterOpen{
    max-width: 300px;
}

.divFilterClosed{
    display: none;
}

.divAddFilterHeader{
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    background:black;
    color:white;
    margin-bottom: 4px;
}

.divAddFilterFooter{
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    background:black;
    color:white;
    margin-top: 4px;
}

.divFilterItem{
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    background:#f3f0ec;
    color:black;
    margin: 2px;
}

.inputFilterValue{
    font-size: 12px;
    max-width: 100px;
}

.divComparisonInput{
    display: flex;
    flex-direction: row;
}
