.btnRemoveFilter{
    background:#d37556;
    border: none;
    border-radius: 10px;
    max-height: 30px;
    max-width: 30px;
}

.btnFilterButtons{
    background:#d37556;
    border: none;
    border-radius: 10px;
    max-height: 30px;
    max-width: 30px;
    
}

